import { Component, OnInit } from '@angular/core';
import { UsuariosLoginService } from '../../providers/Api/UsuariosLogin.service';
import { InicioService } from '../../providers/Almacenamiento/inicio.service';
import { ToastServices } from '../../providers/Utilidades/ToastService.service';
import { trigger, transition, animate, style, stagger, query } from '@angular/animations';
import { CargaElementosUsuariosService } from '../../providers/cargas/usuarios.service';
import { MenuController } from '@ionic/angular';
import { StatusBar } from '@ionic-native/status-bar/ngx';

@Component({
  selector: 'app-buscar',
  templateUrl: './buscar.page.html',
  // animations: [
  //   trigger('usuario', [
  //     transition('* => *', [
  //       query(':enter', [
  //         style({opacity: 0, transform: 'translateX(-200px)'}),
  //         stagger(200, [
  //           animate('40ms', style({ opacity: 1, transform: 'none' }))
  //         ])
  //       ], {optional: true}),

  //       query(':leave', [
  //         style({opacity: 1, transform: 'none'}),
  //         stagger(200, [
  //           animate('40ms', style({ opacity: 0, transform: 'translateX(-200px)' }))
  //         ])
  //       ], {optional: true})
  //     ])
  //   ])
  // ],
  styleUrls: ['./buscar.page.scss'],
})
export class BuscarPage implements OnInit {

  data = [];
  red = 'wifi';

  constructor(
    private apiUsuario: UsuariosLoginService,
    private almacenamiento: InicioService,
    private toast: ToastServices,
    private cargarElementos: CargaElementosUsuariosService,
    private menuCtrl: MenuController,
    private statusbar: StatusBar
  ) { 
    this.cargarElementos.$cargarRed.subscribe((red: any) => {
      this.red = red;
    })

  }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.statusbar.backgroundColorByHexString('#777777');
  }

  buscar( event ) {
    // // console.log( event.detail.value);
    const termino: string = event.detail.value.trimLeft() === '' ? '<<<<<<<<<<<<<<<<<<<<' :  event.detail.value.trimLeft();

    this.almacenamiento.obtenerToken().then((info: any) => {
    //// console.log(info);
    this.apiUsuario.buscarUsuario(termino, info.token).subscribe((usuarios: any) => {
          // for (let i = 0; i <= usuario.response.length; i++)
          this.data = usuarios.response;
          // console.log(this.data);
        }, (error) => {
          this.toast.alertApplication( error.message , false);
        });
     }).catch((e) => {
      this.toast.alertApplication('Error al buscar el usuario', false);
    });
  }

  viajar() {
    // console.log('Holiii');
  }

  abrirMenu() {
    this.menuCtrl.toggle('perfilMenu');
  }
}
